@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&display=swap');

:root {
  color-scheme: dark;
}

body {
  margin: 0;
  font-family: 'Space Grotesk', 'Inter', 'Segoe UI', sans-serif;
  background-color: #000;
  color: #f5f5f5;
}

.app-root {
  min-height: 100vh;
  background: radial-gradient(circle at top, rgba(44, 82, 130, 0.22), transparent 55%),
    radial-gradient(circle at bottom, rgba(86, 30, 136, 0.18), transparent 45%),
    #000;
  display: flex;
  flex-direction: column;
}

.top-bar {
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 48px;
  background: rgba(0, 0, 0, 0.68);
  backdrop-filter: blur(16px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.brand-mark {
  display: flex;
  align-items: center;
  gap: 12px;
  font-weight: 600;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #e9ecff;
}

.brand-glow {
  width: 12px;
  height: 32px;
  border-radius: 8px;
  background: linear-gradient(180deg, #82f2ff, #d88bff);
  box-shadow: 0 0 24px rgba(146, 235, 255, 0.75);
}

.brand-name {
  font-size: 1.1rem;
  letter-spacing: 0.25rem;
}

.nav-tabs {
  display: inline-flex;
  padding: 4px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
  gap: 4px;
}

.nav-tab {
  background: transparent;
  border: none;
  color: #d6daf5;
  padding: 10px 22px;
  border-radius: 999px;
  font-size: 0.95rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  transition: all 0.2s ease;
  cursor: pointer;
}

.nav-tab:hover {
  color: #fff;
}

.nav-tab-active {
  background: linear-gradient(135deg, rgba(130, 242, 255, 0.25), rgba(216, 139, 255, 0.25));
  color: #fff;
  border: 1px solid rgba(130, 242, 255, 0.55);
  box-shadow: 0 4px 18px rgba(130, 242, 255, 0.24);
}

.hero-headline {
  position: relative;
  font-size: clamp(3.5rem, 6vw, 6rem);
  font-weight: 700;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  text-shadow: 0 0 40px rgba(0, 0, 0, 0.75);
}

.hero-subline {
  position: relative;
  font-size: clamp(1.25rem, 2.5vw, 2.25rem);
  max-width: 32rem;
  margin-top: 1.5rem;
  color: #e0f7ff;
  text-shadow: 0 0 24px rgba(0, 0, 0, 0.65);
}

.hero-meta {
  position: relative;
  margin-top: 2rem;
  letter-spacing: 0.4em;
  text-transform: uppercase;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.section-title {
  font-size: clamp(2.25rem, 4vw, 3rem);
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.features-showcase {
  position: relative;
  z-index: 2;
  max-width: 860px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 28px;
  padding: 0 24px;
  text-align: center;
}

.feature-longform {
  width: 100%;
  margin-top: 22px;
  text-align: left;
  color: rgba(228, 246, 255, 0.92);
  line-height: 1.8;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.feature-longform-heading {
  margin: 0;
  font-size: 1.45rem;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}

.feature-longform p {
  margin: 0;
  font-size: 1.05rem;
  color: #DC2626; /* Red color for the long-form paragraphs as requested */
}

.feature-details {
  width: 100%;
  margin-top: 32px;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.feature-details-heading {
  margin: 0;
  font-size: 1.5rem;
  letter-spacing: 0.08em;
  text-transform: uppercase;
}

.feature-details-subtext {
  margin: 0;
  color: rgba(222, 236, 255, 0.85);
  line-height: 1.6;
}

.feature-detail-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 18px;
}

.feature-detail-card {
  background: rgba(12, 18, 34, 0.7);
  border: 1px solid rgba(146, 235, 255, 0.18);
  border-radius: 18px;
  padding: 20px 24px;
  backdrop-filter: blur(16px);
  box-shadow: 0 18px 36px rgba(5, 6, 14, 0.55);
}

.feature-detail-card h4 {
