  font-size: 1.15rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f7ff;
}

.feature-detail-card p {
  margin: 0;
  color: rgba(225, 240, 255, 0.9);
  line-height: 1.7;
}

.features-lede {
