.download-card {
  background: linear-gradient(160deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02));
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 24px;
  padding: 32px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  position: relative;
  overflow: hidden;
  transition: transform 0.25s ease, border-color 0.25s ease, box-shadow 0.25s ease;
  backdrop-filter: blur(18px);
}

.download-card::after {
  content: '';
  position: absolute;
  inset: -40% -20% auto;
  height: 60%;
  background: radial-gradient(circle, rgba(157, 227, 255, 0.3), transparent 65%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.download-card:hover {
  transform: translateY(-6px);
  border-color: rgba(146, 235, 255, 0.6);
  box-shadow: 0 18px 45px rgba(80, 149, 255, 0.16);
}

.download-card:hover::after {
  opacity: 1;
}

.download-header {
  display: flex;
  align-items: center;
  gap: 18px;
}

.platform-icon {
  width: 42px;
  height: 42px;
  fill: rgba(146, 235, 255, 0.85);
  filter: drop-shadow(0 0 12px rgba(146, 235, 255, 0.35));
}

.download-platform {
