.roadmap-section {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 24px 24px 24px;
  background: #000;
  overflow: hidden;
}

.roadmap-section::before {
  content: '';
  position: absolute;
  inset: 0;
  background-image: url('https://rvcfjohfhblhvpnshzbd.supabase.co/storage/v1/object/public/orbitx-application-download/promo/orbitbgro.gif');
  background-size: cover;
  background-position: center;
  filter: blur(20px);
  opacity: 0.5;
  z-index: 0;
}

.roadmap-sheen {
  position: absolute;
  inset: 0;
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0) 35%, rgba(0, 0, 0, 0.9) 100%);
