  text-align: left;
  
}

.roadmap-content h1 {
  margin: 0;
  font-size: clamp(2.4rem, 4vw, 3.4rem);
  letter-spacing: 0.08em;
  text-transform: uppercase;
}

.roadmap-content p {
  margin: 0;
  line-height: 1.2;
