}

.mermaid-chart {
  margin: 12px 0 0;
  padding: 24px;
  border-radius: 18px;
  border: 1px solid rgba(146, 235, 255, 0.35);
  background: rgba(3, 7, 16, 0.85);
  box-shadow: 0 18px 45px rgba(5, 10, 26, 0.65);
  overflow: auto;
  min-height: 220px;
  line-height: 1.2;
  text-align: center;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.mermaid-chart svg {
  width: 100%;
  height: auto;
  overflow: visible; /* allow labels and tails not to be clipped */
}

.mermaid-chart-loading,
.mermaid-chart-error {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(207, 234, 255, 0.85);
  font-weight: 600;
  text-align: center;
}

/* Fix text clipping and ensure correct baseline alignment in Mermaid SVGs */
.mermaid-chart svg text {
  font-family: 'Space Grotesk', 'Inter', 'Segoe UI', sans-serif;
  font-size: 13px;
  line-height: 1.15;
  dominant-baseline: middle; /* Keep labels vertically centered */
  text-anchor: middle;
}

/* Make the node rectangles slightly taller so they won't clip content */
.mermaid-chart svg .node rect {
  rx: 10px;
  ry: 10px;
  /* Increase the rect height slightly via transform scaleY hack where available */
}

/* Make phase nodes 2x taller without scaling text (scale the rect only) */
.mermaid-chart svg .phaseCard rect {
  transform-box: fill-box;
  transform-origin: 50% 50%;
  /* Donâ€™t scale the rect using transforms; we now post-process the SVG heights */
  transform: none !important;
}

.mermaid-chart svg .phaseCard g[class*="label-container"],
.mermaid-chart svg .phaseCard g[class*="label"]{
  overflow: visible !important;
}

.mermaid-chart svg .phaseCard path {
  vector-effect: non-scaling-stroke;
  transform-box: fill-box;
  transform-origin: 50% 50%;
  transform: none !important;
}

/* Ensure outer label containers used by mermaid and 'basic label-container outer-path' do not clip */
.mermaid-chart svg g[class*="label-container"],
.mermaid-chart svg g[class*="label"],
.mermaid-chart svg .basic.label-container.outer-path {
  overflow: visible !important;
  clip-path: none !important;
  mask: none !important;
}

/* Tighten line spacing inside HTML labels (foreignObject) */
.mermaid-chart svg foreignObject div,
.mermaid-chart svg foreignObject span {
  line-height: 1.2 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.mermaid-chart-error {
  color: #ffc9c9;
  border-color: rgba(255, 138, 138, 0.5);
  box-shadow: 0 12px 34px rgba(255, 75, 75, 0.2);
}

.roadmap-note {
