/* Promo video wrapper and styling */
.promo-wrapper {
  width: 100%;
  max-width: 1100px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-bottom: 24px;
  /* Force exact 16:9 aspect ratio at all sizes */
  aspect-ratio: 16 / 9;
}

.promo-video {
  width: 100%;
  height: 100%;
  border-radius: 18px;
  object-fit: cover;
  display: block;
  box-shadow: 0 18px 48px rgba(33, 70, 154, 0.16), 0 0 100px rgba(130, 242, 255, 0.14), 0 0 36px rgba(216, 139, 255, 0.12);
  border: 1px solid rgba(255,255,255,0.06);
}

.promo-fullscreen-btn {
  position: absolute;
  right: 14px;
  bottom: 14px;
  z-index: 6;
